.headerWrapper {
  width: 100%;
  background-color: black;
}
.header {
  display: flex;
  align-items: center;
  gap: 20px;
  /* border: 1px solid red; */
  color: white;
}
.headerLogo {
  transform: rotate(90deg);
  transition: transform var(--time-animation) var(--cubic-bezier);
}
.headerLogo:hover {
  transform: rotate(0deg);
}
